import { DatePipe } from '@angular/common';
import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';
declare var $: any;
import { Loader, LoaderOptions } from 'google-maps';
declare var $: any
@Component({
  selector: 'app-home-cmp',
  templateUrl: './home.component.html'
})

export class HomeComponent implements OnInit, OnDestroy {
  user = null;
  user_id = null;
  searches = null;
  nearbyAds = null;
  moment: any = moment;
  nearByMessage = null;
  lat;
  lng;
  zoom;
  test: Date = new Date();
  step = 0;
  incval = 0;
  incval1 = 0;
  incval2 = 0;
  incval3 = 0;
  incval4 = 0;
  childList = []
  campaignOne: FormGroup;
  campaignTwo: FormGroup;

  // options: LoaderOptions = {/* todo */};
  loader = new Loader('AIzaSyA8S1HrLRkdH_tZAUFuIU9Fu4lDJac757s');

  constructor(private cdf: ChangeDetectorRef,
    public datepipe: DatePipe,
    private dataService: DataService,
    private router: Router) {
    const today = new Date();
    const month = today.getMonth();
    const year = today.getFullYear();

    this.campaignOne = new FormGroup({
      start: new FormControl(new Date(year, month, today.getDay())),
      end: new FormControl(new Date(year, month, today.getDay() + 5))
    });

    this.campaignTwo = new FormGroup({
      start: new FormControl(new Date(year, month, 15)),
      end: new FormControl(new Date(year, month, 19))
    });
  }

  ngOnInit() {
    if (localStorage.getItem("token") != null) {
      this.user = 'exists';
      this.dataService.get('guest/get-saved-searches').then((result) => {
        this.searches = result.data
        this.cdf.detectChanges()
      })
    }

    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];

    this.getPosition().then(pos => {
      let city = '';
      this.loader.load().then(function (google) {
        let geocoder;
        geocoder = new google.maps.Geocoder();
        let latlng = new google.maps.LatLng(pos.lat, pos.lng);
        geocoder.geocode(
          { 'latLng': latlng },
          function (results, status) {
            if (status == google.maps.GeocoderStatus.OK) {
              if (results[0]) {
                let add = results[0].formatted_address;
                let value = add.split(",");
                let count = value.length;
                city = value[count - 3];
                city = city.trim()
                $('#cityName').val(city)
              }
              else {
                this.nearByMessage = "Address Not Found";
              }
            }
            else {
              this.nearByMessage = "Geocoder failed due to: " + status;
            }
          }
        );
      });
    });

    setTimeout(() => {
      this.getNearByAds();
    }, 5000);
  }

  getNearByAds() {
    const data = {
      city: $('#cityName').val()
    }
    this.dataService.post('near-by-ads', data).then((result) => {
      if (result.success == true) {
        if (result.data.length == 0) {
          this.nearByMessage = "No Records Found";
        }
        else {
          this.nearbyAds = result.data[data['city']]
        }
      }
    })
  }

  getCity() {
    this.getPosition().then(pos => {
      let city = '';
      this.loader.load().then(function (google) {
        let geocoder;
        geocoder = new google.maps.Geocoder();
        let latlng = new google.maps.LatLng(pos.lat, pos.lng);
        geocoder.geocode(
          { 'latLng': latlng },
          function (results, status) {
            if (status == google.maps.GeocoderStatus.OK) {
              if (results[0]) {
                let add = results[1].formatted_address;
                let value = add.split(",");
                let count = value.length;
                city = value[count - 3];
                city = city.trim()
                $('#city').val(city)
                if ($('#destinationLabel').text() == "Enter destinantion") {

                  $('#destinationLabel').empty()
                  $('#destinationLabel').text(city)
                }
              }
              else {
                this.nearByMessage = "Address Not Found";
              }
            }
            else {
              this.nearByMessage = "Geocoder failed due to: " + status;
            }
          }
        );
      });
    });
  }

  getDestination() {
    $('#destinationLabel').empty()
    $('#destinationLabel').text($('#city').val())
    this.cdf.detectChanges()
  }

  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  hidecollaps(val) {
    if (val == 1) {
      $('#collapseExample1').removeClass("show");
    }
    else if (val == 2) {
      $('#collapseExample').removeClass("show");
    }
    else {
      $('#collapseExample').removeClass("show");
      $('#collapseExample1').removeClass("show");
    }
  }

  plus(val) {
    switch (val) {
      case 0:
        this.incval += 1;
        break;
      case 1:
        this.incval1 += 1;
        break;
      case 2:
        this.incval2 += 1;
        $('#quantityLabel').empty()
        $('#quantityLabel').text(this.incval2 + ' rooms and ' + (this.incval3 + this.incval4) + ' guests')
        break;
      case 3:
        this.incval3 += 1;
        $('#quantityLabel').empty()
        $('#quantityLabel').text(this.incval2 + ' rooms and ' + (this.incval3 + this.incval4) + ' guests')
        break;
      case 4:
        this.incval4 += 1;
        this.childList.push(this.incval4)
        $('#quantityLabel').empty()
        $('#quantityLabel').text(this.incval2 + ' rooms and ' + (this.incval3 + this.incval4) + ' guests')
        this.cdf.detectChanges()
        break;
    }
  }

  minus(val) {
    switch (val) {
      case 0:
        if (val == 0) {
          if (this.incval > 0) {
            this.incval -= 1;
            break;
          }
        }
      case 1:
        if (val == 1) {
          if (this.incval1 > 0) {
            this.incval1 -= 1;
            break;
          }
        }
      case 2:
        if (val == 2) {
          if (this.incval2 > 0) {
            this.incval2 -= 1;
            break;
          }
        }
        $('#quantityLabel').empty()
        $('#quantityLabel').text(this.incval2 + ' rooms and ' + (this.incval3 + this.incval4) + ' guests')
      case 3:
        if (val == 3) {
          if (this.incval3 > 0) {
            this.incval3 -= 1;
            break;
          }
        }
        $('#quantityLabel').empty()
        $('#quantityLabel').text(this.incval2 + ' rooms and ' + (this.incval3 + this.incval4) + ' guests')
      case 4:
        if (val == 4) {
          if (this.incval4 > 0) {
            this.incval4 -= 1;
            this.childList.pop()
            this.cdf.detectChanges()
            break;
          }
        }
        $('#quantityLabel').empty()
        $('#quantityLabel').text(this.incval2 + ' rooms and ' + (this.incval3 + this.incval4) + ' guests')
    }
  }

  getPosition(): Promise<any> {
    return new Promise((resolve, reject) => {

      navigator.geolocation.getCurrentPosition(resp => {
        resolve({ lng: resp.coords.longitude, lat: resp.coords.latitude });
      },
        err => {
          this.nearByMessage = "anable"
          console.log(err)
          reject(err);
        });
    });

  }
  getLocation1() {
    // If the user allow us to get the location from the browser
    if (window.location.protocol == "https:" && navigator.geolocation)
      navigator.geolocation.getCurrentPosition(function (position) {
        // geolocation["lat"] = position.coords.latitude;
        // geolocation["long"] = position.coords.longitude;
        // printLocation(); // Second time, will be return the location correctly
      });
    else {
      // We cannot access to the geolocation
    }
  }

  getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {
        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;
        this.zoom = 16;
        console.log("position", position)
      });
    } else {
      console.log("User not allowed")
    }
  }

  dateRange() {
    let startDate = this.datepipe.transform(this.campaignOne.controls['start'].value, 'yyyy-MM-dd');
    let endDate = this.datepipe.transform(this.campaignOne.controls['end'].value, 'yyyy-MM-dd')
    $('#dateLabel').empty()
    $('#dateLabel').text(startDate + ' to ' + endDate)
    this.cdf.detectChanges()
  }

  search() {
    const data = {
      destination: $('#city').val(),
      from: this.datepipe.transform(this.campaignOne.controls['start'].value, 'yyyy-MM-dd'),
      to: this.datepipe.transform(this.campaignOne.controls['end'].value, 'yyyy-MM-dd'),
      rooms: this.incval2,
      // adults: this.incval3,
      // children: this.incval4,
      guests: this.incval3 + this.incval4,
      user_id: this.user_id
    }

    if (data['destination'] == null || data['from'] == null || data['to'] == null || data['rooms'] <= 0 || data['guests'] <= 0) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        title: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      return;
    }
    this.searchResults(data)
  }

  getSavedSearch(destination, from, to, rooms, adults, children, guests) {
    const data = {
      destination: destination,
      from: from,
      to: to,
      rooms: rooms,
      // adults: adults,
      // children: children,
      guests: guests, //adults + children,
      user_id: this.user_id
    }
    this.searchResults(data)
  }

  saveCheck() {
    if ($('#save').prop("checked") == true) {
      this.user_id = localStorage.getItem('id')
    }
    else if ($(this).prop("checked") == false) {
      this.user_id = null;
    }
  }

  searchResults(data) {
    this.dataService.post('search', data).then((result) => {
      localStorage.setItem('city', data['destination']);
      localStorage.setItem('from', data['from']);
      localStorage.setItem('to', data['to']);
      localStorage.setItem('rooms', data['rooms']);
      localStorage.setItem('guests', data['guests']);
      let searchedData = null
      for (const key in result.data) {
        if (key.toLocaleLowerCase().includes(data['destination'].toLocaleLowerCase())) {
          searchedData = result.data[key]
          break
        }
      }
      localStorage.setItem('data', JSON.stringify(searchedData))
      this.cdf.detectChanges()
      this.router.navigate(['guest/searchresult'])
    })
  }

  delete(id) {
    const data = {
      id: id
    }

    this.dataService.post('guest/delete-saved-search', data).then((result) => {
      this.searches.splice(id);
      this.cdf.detectChanges();
      Swal.fire({
        position: 'center',
        icon: 'success',
        title: 'Success',
        showConfirmButton: false,
        timer: 2500
      })
    })
  }

  getOfferDetails(id) {
    this.router.navigate(['guestportal/offerdetails', id])
  }

  host() {
    this.router.navigate(['host/home'])
  }
}